.skillBarContainer {
  width: 100%;
  height: 31px;
  background: #C4C4C4;
  display: flex;
  font-weight: 500;
  font-size: 18px;
  line-height: 31px;
  padding-right: 10px;
  color: #000000;
}
.skillBarContainer span {
  opacity: 0;
}

.skill {
  height: 100%;
  width: 130px;
  background: #585858;
  margin: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  color: #FFFFFF;
  text-align: center;
  line-height: 31px;
}
.skill span {
  opacity: 1;
}

.statsContainer {
  width: 100%;
  margin: 0;
  height: 100%;
}

.stats {
  width: 1%;
  margin: 0;
  height: 100%;
  background: #2E7BC1;
}