.aboutHero {
  background-image: url("/static/images/hero/about_top.png");
  height: 600px;
}

.skillHero, .workHero, .educationHero {
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 250px;
}

.skillHero {
  background-image: url("/static/images/hero/about_skill.png");
}

.workHero {
  background-image: url("/static/images/hero/about_work.png");
}

.educationHero {
  background-image: url("/static/images/hero/about_education.png");
}

.yasminBoxContainer {
  border: red 2px dotted;
  height: 200px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.box {
  height: 50px;
  width: 50px;
  background-color: brown;
}