.specialtiesContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 40px auto;
}

.ml img {
  content: url("/static/images/specialties/ml.png");
}

.robot img {
  content: url("/static/images/specialties/robot.png");
}

.nlp img {
  content: url("/static/images/specialties/nlp.png");
}

.se img {
  content: url("/static/images/specialties/se.png");
}

.ml:hover, .bigdata:hover,
.nlp:hover, .se:hover {
  animation-play-state: paused;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
@keyframes rotation-anti {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-359deg);
  }
}
@keyframes rotate-back {
  to {
    transform: rotate(0deg);
  }
}