.footer {
  height: 300px;
  width: 100%;
  background: #292929;
  z-index: -1;
  padding: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-top: 350px;
}

.socmedContainer {
  width: 25%;
  padding: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.socmed {
  width: 50px;
  height: 50px;
  margin: 0;
  display: flex;
  align-items: center;
  background-color: rgba(196, 196, 196, 0.5);
  opacity: 0.85;
}

.socmed:hover {
  opacity: 1;
}

.insta, .fb, .email,
.linkedin, .youtube {
  width: 90%;
  height: 90%;
  z-index: 1;
  opacity: inherit;
}

.insta {
  content: url("/static/images/icons/instagram.png");
}

.fb {
  content: url("/static/images/icons/facebook.png");
}

.email {
  content: url("/static/images/icons/email.png");
}

.linkedin {
  content: url("/static/images/icons/linkedin.png");
}

.youtube {
  content: url("/static/images/icons/youtube.png");
}

.text {
  font-size: 18px;
  color: #FFFFFF;
}

.year {
  color: #FF0000;
}