.hexagonContainer {
  width: 130px;
  height: 130px;
  position: relative;
}

.hexagonContainer:hover {
  transform: rotate(0deg);
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
.ml {
  content: url("/static/images/specialties/ml.png");
}

img {
  z-index: 1;
  position: absolute;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 60px;
  height: 60px;
}

.hexagon {
  width: 85%;
  height: 48.5%;
  margin: auto;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: #C4C4C4;
}

.hexagon::before, .hexagon::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  background: #C4C4C4;
}

.hexagon::before {
  transform: rotate(60deg);
}

.hexagon::after {
  transform: rotate(-60deg);
}